import { PostData } from '@/app/domain/posts/post';
import { fetchAPI } from './fetch-helper';

// Recebe o slug (string) e retorna UM post ou null
export const getPostBySlug = async (slug: string): Promise<PostData | null> => {
  const urlParams = {
    // üëá AQUI EST√Å O SEGREDO: FILTRO
    filters: {
      slug: {
        $eq: slug, // $eq significa "Equal" (Igual)
      },
    },
    // Precisamos popular para pegar imagem e texto
    populate: {
      cover: { fields: ['url', 'alternativeText', 'width', 'height'] },
      category: { populate: true },
      author: true,
    },
  };

  const json = await fetchAPI('/posts', urlParams);

  // O Strapi sempre retorna um array quando usamos filters.
  // Se o array tiver itens, pegamos o primeiro ([0]). Sen√£o, retornamos null.
  const posts = json.data;

  return posts.length > 0 ? posts[0] : null;
};
